import React, { Component, Suspense } from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import withRouter from '../../../../routes/WithRouter';
import { Container, Button, Row, Col } from 'react-bootstrap';
import { GET_WEBSITE_LIST } from '../../../../../actions/websiteAction';
import { FontAwesomeIcon } from '../../../../../Assets/AssetsHelper';
import { GET_ALL_SHOPON_LIST } from '../../../../../actions/shoponAction';
import { GET_DASHBOARD_DATA } from '../../../../../actions/sceneActions';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import ErrorFound from '../../../../ErrorFound';
import "../home.scss"

const CaseStudy = React.lazy(() => import("./CaseStudy"));
const Overview = React.lazy(() => import("./Overview"));
const SocialAccount = React.lazy(() => import("./SocialAccount"));
const OverallCount = React.lazy(() => import("./OverallCount"));
const PublishAssets = React.lazy(() => import("./PublishAssets"));
const TrustedApps = React.lazy(() => import("./TrustedApps"));
const ChannelPerfomance = React.lazy(() => import("./ChannelPerfomance"));
const ContentChart = React.lazy(() => import("./ContentChart"));
const ActiveUsers = React.lazy(() => import("./ActiveUsers"));
const TopPosts = React.lazy(() => import("./TopPosts"));
const TopProducts = React.lazy(() => import("./TopProducts"));



const dateRanges = {
    "Today": [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
    "Yesterday": [moment().subtract(1, 'days').format("DD-MM-YYYY"), moment().subtract(1, 'days').format("DD-MM-YYYY"),],
    'Last 7 Days': [moment().subtract(6, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
    'Last 30 Days': [moment().subtract(30, 'days').format("DD-MM-YYYY"), moment().format("DD-MM-YYYY"),],
    'Last Month': [moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"), moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),],
}

class Dashboard extends Component {
    state = { loadDelay: 0, startDate: moment().subtract(30, 'days').format("DD-MM-YYYY"), endDate: moment().format("DD-MM-YYYY"), maxDate: moment().endOf('month').format("DD-MM-YYYY") }

    componentDidMount() {
        const { getDashboard, websiteList, getWebistes, shopOnList, getShopOn } = this.props
        const { startDate, endDate } = this.state
        getDashboard(startDate, endDate);
        if (!(websiteList && websiteList.length)) getWebistes()
        if (!(shopOnList && shopOnList.length)) getShopOn()

         //setTimeout(() =>  this.setState({ loadDelay : 1}), 10000);   
    }

    handleOnApply = (event, picker) => this.setState({ startDate: picker.startDate.format("DD-MM-YYYY"), endDate: picker.endDate.format("DD-MM-YYYY") })

    handleCallback = (start, end) => {
        this.props.getDashboard(start, end)
        this.setState({ startDate: start.format("DD-MM-YYYY"), endDate: end.format("DD-MM-YYYY") })
    }

    render() {
        const { startDate, endDate, maxDate } = this.state

        return <Container>
            <div className="text-end w-100">
                <ErrorFound>
                    <DateRangePicker onApply={this.handleOnApply} startDate={startDate} endDate={endDate}
                        initialSettings={{
                            locale: { format: 'DD-MM-YYYY', separator: '-' },
                            maxDate: maxDate,
                            showDropdowns: true,
                            startDate: startDate, endDate: endDate,
                            ranges: dateRanges,
                        }} onCallback={this.handleCallback}>
                        <Button variant='secondary' className='mb-5 f-center flex-grow-1' style={{minWidth:190}}>
                            <FontAwesomeIcon icon={'calendar-range'} iconClass={'me-1'}/>
                            {moment(startDate, "DD-MM-YYYY").format("ll")}
                            -
                            {moment(endDate, "DD-MM-YYYY").format("ll")}
                        </Button>
                    </DateRangePicker>
                </ErrorFound>
            </div>
            <Suspense fallback={null}><CaseStudy /></Suspense>
            <Suspense fallback={null}><Overview /></Suspense>
            <Row>
                <ErrorFound>
                <Suspense fallback={null}><SocialAccount /></Suspense>
                </ErrorFound>
                <Col sm={6}>
                <Suspense fallback={null}><OverallCount title={'Gallery Count'} tooltip={'No. of galleries created vs the total no. of galleries available in your plan.'} gallery={true} /></Suspense>
                <Suspense fallback={null}><OverallCount title={'Feed count'} tooltip={'No. of feeds created vs the total no. of feeds available in your plan.'} /></Suspense>
                </Col>
            </Row>
            <Suspense fallback={null}><PublishAssets /></Suspense>
            <Suspense fallback={null}><TrustedApps /></Suspense>
            <Suspense fallback={null}><ChannelPerfomance startDate={startDate} endDate={endDate} /></Suspense>
                 { this.state.loadDelay ?
                    <Row> 
                        <Col lg={6}>
                            <ErrorFound> <Suspense fallback={null}><ContentChart/></Suspense></ErrorFound>
                        </Col>
                        <Col lg={6}>
                            <ErrorFound> <Suspense fallback={null}><ActiveUsers /></Suspense></ErrorFound>
                        </Col>
                        <Col lg={6}>
                            <ErrorFound> <Suspense fallback={null}><TopPosts /></Suspense></ErrorFound>
                        </Col>
                        <Col lg={6}>
                            <ErrorFound> <Suspense fallback={null}><TopProducts /></Suspense></ErrorFound>
                        </Col>
                    </Row> : null }
        </Container>
    }
}

const mapStateToProps = state => {
    return {
        websiteList: state.websiteList?.websiteList,
        shopOnList: state.shopOnList?.shopOnList,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getDashboard: (start, end) => dispatch(GET_DASHBOARD_DATA(start, end)),
        getWebistes: () => dispatch(GET_WEBSITE_LIST()),
        getShopOn: () => dispatch(GET_ALL_SHOPON_LIST()),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(withRouter(React.memo(Dashboard)))